import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { PatinadasService } from 'src/app/providers/Api/Patinadas.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-patinadas-mias',
  templateUrl: './patinadas-mias.page.html',
  styleUrls: ['./patinadas-mias.page.scss'],
})
export class PatinadasMiasPage implements OnInit {

  patinadas = [];
  cargado = false;
  id = '';

  constructor(
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private router: Router,
    private alert: AlertController,
    private apiPatinada: PatinadasService,
    private modal: ModalController,
    private socket: Socket
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.patinadas = [];
    this.traer();

  }

  refrescando(event) {
    this.traer().then(() => {
      event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  async traer() {
    this.patinadas = [];
    const info = await this.almacenamiento.obtenerToken();


    if (info ) {

      this.id = info._id;

      this.apiPatinada.consultarPatinadaMias(info.token).subscribe((data: any) => {
        console.log(data, 'wu')
        if (data.exe) {
          data.response.forEach((item) => {
            item.vs = false;
            this.patinadas.push(item);
          })
          this.cargado = true;
        } else {
          this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
          this.router.navigate(['/central/menu']);
        }
      }, (err) => {
        this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
        this.router.navigate(['/central/menu']);
      })
    }

  }


  
  async cancelar(patinada) {

    const alert = await this.alert.create({
      header: 'Atención!',
      // tslint:disable-next-line:max-line-length
      message: '¿Quieres finalizar esta patinada? al aceptar ya nadie podrá ver tu solicitud en la bandeja de patinadas.',
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Finalizar',
          cssClass: 'botonalerta',
          handler: async () => {
            const info = await this.almacenamiento.obtenerToken();


            if (info ) {
        
              const dispositivos = patinada.tomados.map((item) => {
                if (item._id !== this.id) {
                  return item.usuarioID;
                }
              })
              this.apiPatinada.patinadaEliminar(info.token, {id: patinada._id, accesos: patinada.tomados, dispositivos: dispositivos}).subscribe((data: any) => {
              //  console.log(data, 'wu')
                if (data.exe) {
                 this.toast.Especial('Patinada finalizada');
                 this.traer();
                } else {
                  this.toast.toastError('No se pudo eliminar las patinadas, inténtelo más tarde');
                  this.router.navigate(['/central/menu']);
                }
              }, (err) => {
                this.toast.toastError('No se pudo eliminar las patinadas, inténtelo más tarde');
                this.router.navigate(['/central/menu']);
              })
            }
          }
        }
      ]
    })

    await alert.present();


   

  }


  mostrar(i) {
    this.patinadas[i].vs = !this.patinadas[i].vs;
  }



  async navegar(id) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiPatinada.consultarChat(info.token, {patinada: id}).subscribe((data: any) => {
         //  console.log(data)
          if (data.exe) {
            if (data.response.length > 0) {
              if (data.response[0].accesos.length > 0) {
                this.almacenamiento.insertarChat(data.response[0]).then(() => {
                  this.router.navigate(['/central/chats/chat']);
                 
                }).catch(() => {
                  this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
                })
              } else {
                this.toast.Especial('Este chat ya no tiene usuarios por dentro');
              }
            } else {
              this.toast.toastError('No se encuentra la sala de chat, seguramente la patina ya ha sido cancelada');
            }
            
          } else {
            this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
           
          }
        }, (err) => {
          this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
        })
      }


   
  }


  
  async navegarChat(id, usuario) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiPatinada.consultarChat(info.token, {patinada: id, usuario}).subscribe((data: any) => {
         //  console.log(data)
          if (data.exe) {
            if (data.response.length > 0) {
              if (data.response[0].accesos.length > 0) {
                this.almacenamiento.insertarChat(data.response[0]).then(() => {
                  this.router.navigate(['/central/chats/chat']);
                 
                }).catch(() => {
                  this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
                })
              } else {
                this.toast.Especial('Este chat ya no tiene usuarios por dentro');
              }
            } else {
              this.toast.toastError('No se encuentra la sala de chat, seguramente la patina ya ha sido cancelada');
            }
            
          } else {
            this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
           
          }
        }, (err) => {
          this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
        })
      }


   
  }

}
